import PressTabs from 'press-ui/press-tabs/press-tabs.vue';
import PressTab from 'press-ui/press-tab/press-tab.vue';
import pressIconPlus from 'press-ui/press-icon-plus/press-icon-plus.vue';

export default {
  i18n: {
    'zh-CN': {
      tab: '标签 ',
      title2: '标签栏滚动',
      title3: '禁用标签',
      title4: '样式风格',
      title5: '点击事件',
      title6: '粘性布局',
      title7: '自定义标签',
      title8: '切换动画',
      title9: '滑动切换',
      title10: '滚动导航',
      horTitle: 'HOR',
      disabled: ' 已被禁用',
      matchByName: '通过名称匹配',
      beforeChange: '异步切换',
      lazyTab: '延迟加载',
      slot: '插槽',
    },
    'en-US': {
      tab: 'Tab ',
      content: 'content of tab',
      title2: 'Swipe Tabs',
      title3: 'Disabled Tab',
      title4: 'Card Style',
      title5: 'Click Event',
      title6: 'Sticky',
      title7: 'Custom Tab',
      title8: 'Switch Animation',
      title9: 'Swipeable',
      title10: 'Scrollspy',
      horTitle: 'HOR',
      disabled: ' is disabled',
      matchByName: 'Match By Name',
      beforeChange: 'Before Change',
      lazyTab: 'LAZY TAB',
      slot: 'Slot',
    },
  },
  components: {
    PressTabs,
    PressTab,
    pressIconPlus,
  },
  data() {
    return {
      showLazyTab: false,
      active: 3,
      activeName: 'c',
      tabs2: [1, 2],
      tabs4: [1, 2, 3, 4],
      tabs7: [1, 2, 3, 4, 5, 6, 7],
      tabsDisabled: [{ dot: true }, {}, { disabled: true }, {}],
      tabsWithName: [
        { name: 'a' },
        { name: 'b' },
        { name: 'c' },
      ],
    };
  },
  mounted() {
    setTimeout(() => {
      this.tabs4 = [1, 2, 3];
      this.active = 2;

      setTimeout(() => {
        this.showLazyTab = true;
      }, 1000);
    }, 2000);
  },
  methods: {
    onChange(val) {
      console.log('[onChange] val: ', val);
    },
    onInput(val) {
      console.log('[onInput] val: ', val);
    },
    onClickDisabled(val) {
      console.log('[onClickDisabled] val: ', val);
    },
    onClick(val) {
      console.log('[onClick] val: ', val);
      const { index, name, title } = val;
      this.onGTip(`index: ${index}, name: ${name}, tile: ${title}`);
    },
    onScroll(val) {
      console.log('[onScroll] val: ', val);
    },
  },
};
